import {Fragment,useEffect} from "react";
import {useRecoilState} from "recoil";
import {currentObj, navigation, sectionApp, sections} from "../../states/globalStates";
import {gsapAnimations} from "../Script";
import {boxerData, clothe} from "../../states/data";


export const StickyBottom = ()=>{

  const [nav,setNav] = useRecoilState(navigation);
  const [cam,setCam] = useRecoilState(sectionApp);
  const [current,setCurrent] = useRecoilState(currentObj);
  const [section,setSection] = useRecoilState(sections);


  const animationClickTwo = () =>{
    /*setNav(2)
    setCam('initBoxer')
    gsapAnimations('initBoxer')
    setCurrent(boxerData[0])
    setSection(boxerData)*/
    window.location.href='https://sites.google.com/misena.edu.co/ivcs/productos/camiseta-tipo-polo?authuser=2'
  }

  const animationClickOne = () =>{
    setNav(1)
    setCam('frontCamera')
    gsapAnimations('frontCamera')
    setCurrent(clothe[0])
    setSection(clothe)
  }

  return(
      <Fragment>

          {
            nav === 1 && (
              <div className="btnBackRight">
                <button
                  onClick={(e) => animationClickTwo(e)}
                  className="btn ">
                <span className="orangeColor fs-4 fw-bolder">
                  Regresar 
                </span>
                </button>
              </div>
            ) || nav === 2 && (
              <div className="btnBackLeft">
                <button
                  onClick={animationClickOne}
                  className="btn ">
                <span className="orangeColor fs-4 fw-bolder">
                   Camiseta polo
                </span>
                </button>
              </div>
            )
          }

      </Fragment>
  )
}