import {Fragment} from "react";
import Container from 'react-bootstrap/Container';
import {Navbar, NavDropdown, Nav} from 'react-bootstrap';
import {useRecoilState} from "recoil";
import {currentObj, sectionApp, sections} from "../states/globalStates";
import {gsapAnimations} from "./Script";
export const NavBarMobile = () =>{
  const [cam,setCam] = useRecoilState(sectionApp);
  const [current,setCurrent] = useRecoilState(currentObj);
  const [section,setSection] = useRecoilState(sections);

  const filter = (id) =>{
    const currentFilter = section.filter(sec => sec.id === id )
    setCurrent(currentFilter[0])
  }
  return(
    <Fragment>
      <Navbar collapseOnSelect expand="lg">
        <Container>
          <Navbar.Brand
            onClick={()=> {
              setCam('frontCamera')
              gsapAnimations('frontCamera')
              filter('frontCamera')
              //animationFront()
            }}
          >
             <span className="orangeColor" >
                 Especificaciones
              </span>

          </Navbar.Brand>
          <Navbar.Toggle />
          <Navbar.Collapse className="justify-content-end">




            <Nav.Link onClick={()=> {
              setCam('topClothe')
              gsapAnimations('topClothe')
              filter('topClothe')
            }}
            >
              <span className="orangeColor" >
                 Cuello
              </span>


            </Nav.Link>



            <Nav.Link onClick={()=> {
              setCam('neck')
              gsapAnimations('neck')
              filter('neck')
            }}
            >
              <span className="orangeColor" >
                 Perilla
              </span>

            </Nav.Link>

            <Nav.Link onClick={()=> {
              setCam('sleeve')
              gsapAnimations('sleeve')
              filter('sleeve')
            }}
            >
              <span className="orangeColor" >
                 Puo
              </span>

            </Nav.Link>

            <Nav.Link onClick={()=> {
              setCam('bottomEdge')
              gsapAnimations('bottomEdge')
              filter('bottomEdge')
            }}
            >
              <span className="orangeColor" >
                 Ruedo bajo
              </span>

            </Nav.Link>
          </Navbar.Collapse>
        </Container>
      </Navbar>








    </Fragment>
  )
}