
import {gsapAnimations} from "../../Script";
import {useRecoilState} from "recoil";
import {currentObj, navigation, sectionApp, sections} from "../../../states/globalStates";

import {Fragment, useEffect} from "react";
import {boxerData} from "../../../states/data";

const  InfoBoxer = () => {
  const [cam,setCam] = useRecoilState(sectionApp);
  const [current,setCurrent] = useRecoilState(currentObj);
  const [section,setSection] = useRecoilState(sections);

  useEffect(()=>{
    setSection(boxerData)
  })

  const filter = (id) =>{
    const currentFilter = section.filter(sec => sec.id === id )
    setCurrent(currentFilter[0])
  }

  return (

    <Fragment>

      <a
        href="#"
        onClick={()=> {
          setCam('initBoxer')
          gsapAnimations('initBoxer')
          filter('initBoxer')
          //animationFront()
        }}
        className="facebook">
        Especificaciones Tcnicas de Calidad
      </a>

      <a
        href="#"
        onClick={()=> {
          setCam('topBoxer')
          gsapAnimations('topBoxer')
          filter('topBoxer')
        }}
        className="twitter1">
        Delantero costado
      </a>
      <a
        href="#"
        onClick={()=> {
          setCam('backBoxer')
          gsapAnimations('backBoxer')
          filter('backBoxer')
        }}
        className="twitter">
        trasero
      </a>
      <a
        href="#"
        onClick={()=> {
          setCam('middleBoxer')
          gsapAnimations('middleBoxer')
          filter('middleBoxer')
        }}
        className="google">
        Delantero medio
      </a>
      <a
        href="#"
        onClick={()=> {
          setCam('middleBoxerInt')
          gsapAnimations('middleBoxerInt')
          filter('middleBoxerInt')
        }}
        className="linkedin">
        Centro delantero interno
      </a>

    </Fragment>

  );
}

export default InfoBoxer;
