
import {
    atom
} from 'recoil';
import {clothe, clothe1} from "./data";

export const sectionApp = atom({
    key: 'sectionApp', // unique ID (with respect to other atoms/selectors)
    default: '', // default value (aka initial value)
});

export const sections = atom({
    key: 'sections', // unique ID (with respect to other atoms/selectors)
    default: clothe, // default value (aka initial value)
});

export const currentObj = atom({
    key: 'currentObj', // unique ID (with respect to other atoms/selectors)
    default: clothe[0], // default value (aka initial value)
});

export const navigation = atom({
    key: 'navigation', // unique ID (with respect to other atoms/selectors)
    default: 1, // default value (aka initial value)
});
